<!-- App.vue est le point d'entrÃ©e de notre application Vue
	Il contient les donnÃ©es que nous allons "propager" Ã  travers toute les pages qui sont :
	articles: le tableau des articles provenant de la tables "articles" de notre BDD,
	panier: objet contenant la date de crÃ©ation, la date de la derniÃ¨re maj et le tableau d'articles ajoutÃ©s au la panier
	user: objet contenant les informations de l'utilisateur tel que sont ID ainsi que son adresse email,
	isConnected: boolÃ©en indiquant si l'utilisateur est connectÃ©,
	
	- Contient le Header ainsi que le Footer ce qui permet de les afficher pour chaque page.
	contient la router-view permettant d'afficher le contenu des components Vue qui seront appellÃ©s
	contient les "Ã©couteurs d'Ã©vÃ¨nements" afin de pouvoir rÃ©aliser les opÃ©rations lors des actions de l'utilisateurs
	tel que la rÃ©cupÃ©ration de donnÃ©es vers les diffÃ©rentes requÃªtes d'API et des opÃ©rations sur les donnÃ©es de l'application.

-->
<template>
  <div id="app">
    
    <Header :panier="panier" :is-connected='isConnected'/>
	<router-view 
		:articles="articles"
		:panier="panier"
		:user="user"
		:is-connected="isConnected"
		@add-article="addArticle"		
		@add-to-panier="addToPanier"
		@connexion="connexion"
		@change-quantity="changeQuantity"
		@remove-from-panier="removeFromPanier"
		@delete-article="deleteArticle"
		@pay="pay"
		@deconnexion="deconnexion">
	</router-view>
	<Footer/>
    
  </div>
</template>

<script>
import Header from './components/Header'
import Footer from './components/Footer'
import axios from 'axios'
export default{
	name:'App',
	components:{
		Header,
		Footer
	},
	// donnÃ©es de notre application Vue
	data() {
		return{
			articles: [],
			panier: {
				createdAt: null,
				updatedAt: null,
				articles: []
			},
			user: {},
			isConnected: false,
		}
	},
	// requÃªtes Ã  effectuer vers l'API pour rÃ©cupÃ©rer nos donnÃ©es 
	// s'effectuent dÃ¨s le montage du component
	async mounted(){
		let res = await axios.get("/api/articles");
		this.articles = res.data;
		let res2 = await axios.get("/api/panier");
			this.panier = res2.data;
		try{
			const res3 = await axios.get('/api/me')
			this.user = res3.data
			this.isConnected = true
		}catch(err){
			if(err.response?.status === 401){
				this.isConnected = false
			}else{
				console.log('error', err)
			}
		}
	},

	// mÃ©thode executÃ©es lorsque les Ã©couteurs dÃ©tectent l'Ã©vÃ¨nement correspondant
	methods:{
		
		async connexion(user){
			const res =  await axios.post('/api/connexion',user)
			this.user = res.data
			this.isConnected = true
			this.$router.push('/')
		},
		async addToPanier(articleId,selectedSize) {
			if(this.panier.articles === undefined){
				this.panier.articles = new Array()
			}
			const newArticle = { id: articleId, quantity: 1 ,size: selectedSize}
			const res = await axios.post('/api/panier',newArticle)
			console.log(res)
			this.panier.articles.push(newArticle)
		},

		async changeQuantity({ articleId, quantity }) {
			const res = await axios.put('/api/panier/' + articleId, { quantity })
			const article = this.panier.articles.find(a => a.id === articleId)
			console.log(res)
			console.log(article)
			article.quantity = quantity
		},

		async removeFromPanier(articleId) {
			await axios.delete('/api/panier/' + articleId)
			const index = this.panier.articles.findIndex(a => a.id === articleId)
			this.panier.articles.splice(index, 1)
		},
		async pay() {
			await axios.post('/api/panier/pay')
			this.panier.articles = []
			this.$router.push('/')	
    },
		async deleteArticle(articleId) {
			await axios.delete('http://localhost:5000/api/article/' + articleId)
			console.log(this.articles.length)
			const index = this.articles.findIndex(a => a.id === articleId)
			console.log(index)
			this.articles.splice(this.articles.length-index, 1)
		},

		async addArticle(article){
			const res = await axios.post('http://localhost:5000/api/article', article)
			this.articles.push(res.data)
		},

		async deconnexion(){
			await axios.delete('http://localhost:5000/api/deconnexion')
			this.isConnected = false
		}
	}
}
</script>

<style>
   * {
			margin: 0;
			padding: 0;
			box-sizing: border-box;
			
		}

		body {
			margin: 20px 20px;
			font-family: "Montserrat", sans-serif;
			background-color: #f7f7f7;
		}

    
		a,
		a:visited,
		a:link {
			color: black;
			font-weight: bold;
			text-decoration: none;
		}

		a:hover {
			transform: scale(1.2);
    }
    
     

</style>